import React from "react";
import WideTestimonial from "components/WideTestimonial";

import { Wrapper, Title, TestimonialList } from "./elements";

import { testimonials } from "./messages";

const TestimonialSection = () => (
  <Wrapper>
    <Title>Hear what our customers have to say</Title>
    <TestimonialList>
      {testimonials.map((testimonial) => (
        <WideTestimonial testimonial={testimonial} key={testimonial.key} />
      ))}
    </TestimonialList>
  </Wrapper>
);

export default TestimonialSection;
